import React,{useState} from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import logo from '../assets/logo.png'
import {Link} from 'react-router-dom';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch} from '@fortawesome/free-solid-svg-icons';
import {connect} from 'react-redux';
import * as categories from './Data/categories.json';


function Header(props) {
    const [search, setSearch] = useState(['']);

    return (
        <div className="header">
            <div className='top-header'>
                <div className='container'>
                    <div className='top-header-nav nav-global'>
                        <div class="ng-item-wrap ng-help-wrap">
                            <div className="ng-item ng-help ng-sub"><span className="ng-sub-title">Help</span></div>
				            <div className="ng-item ng-bp"><a href="//sale.aliexpress.com/v8Yr8f629D.htm" >Buyer Protection</a></div>
				            <div className="ng-item ng-mobile"><a href="//sale.aliexpress.com/download_app_guide.htm">App</a></div>
			            </div>
                        <div className="ng-item-wrap ng-personal-info">
                            <div class="ng-item nav-pinfo-item nav-wishlist">
                                <a href="//my.aliexpress.com/wishlist/wish_list_product_list.htm">
                                    <i class="ng-wishlist-icon ng-icon-size"></i>
                                    <span class="text">Wish List</span> 
                                </a>
                            </div>
                            <div class="ng-item nav-pinfo-item nav-user-account">
                                <span class="user-account-port">
                                    <a data-role="myaliexpress-link">
                                        <span class="text">Account</span> 
                                    </a> 
                                </span>
                                <div class="user-account-main">
                                    <div class="flyout-user-signIn flyout-new-user">
                                        <p class="flyout-welcome-wrap">Welcome to AliExpress!</p>
                                        <div class="flyout-logined">
                                            <i class="flyout-user-avatar">
                                                <img alt="" data-role="avatar-img" src=""/>
                                            </i>
                                            <p class="flyout-welcome-text" >Welcome to AliExpress.com</p>
                                        </div>
                                        <p class="flyout-sign-out" >
                                            <a href="//login.aliexpress.com/xman/xlogout.htm" >Sign Out</a>
                                        </p>
                                        <p class="flyout-bottons">
                                            <a class="join-btn" data-role="join-link" href="javaScript:;" rel="nofollow" data-spm-anchor-id="a2g0o.best.1000001.29">Join</a>
                                            <a class="sign-btn" data-role="sign-link" href="javaScript:;" rel="nofollow" data-spm-anchor-id="a2g0o.best.1000001.30">Sign in</a>
                                        </p>
                                    </div>
                                    <i class="flyout-line">&nbsp;</i>
                                    <ul class="flyout-quick-entry" data-role="quick-entry">
                                        <li><a href="//trade.aliexpress.com/orderList.htm?tracelog=ws_topbar" rel="nofollow" data-spm-anchor-id="a2g0o.best.1000001.31">My Orders</a></li>
                                        <li><a href="//msg.aliexpress.com?tracelog=ws_topbar" rel="nofollow" data-spm-anchor-id="a2g0o.best.1000001.32">Message Center<span class="unread-message-count"></span></a></li>
                                        <li><a href="//my.aliexpress.com/wishlist/wish_list_product_list.htm" rel="nofollow" data-spm-anchor-id="a2g0o.best.1000001.33">Wish List</a></li>
                                        <li><a class="js-menu-my-favorite-stores" href="//my.aliexpress.com/wishlist/wish_list_store_list.htm" rel="nofollow" data-spm-anchor-id="a2g0o.best.1000001.34">My Favorite Stores</a></li>
                                        <li><a href="//coupon.aliexpress.com/buyer/coupon/listView.htm" rel="nofollow" data-spm-anchor-id="a2g0o.best.1000001.35">My Coupons</a></li>
                                        <li><a href="//campaign.aliexpress.com/wow/gcp/old-with-new/index?_addShare=no" rel="nofollow" data-spm-anchor-id="a2g0o.best.1000001.36">Invite friends to give US $24</a></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="container">
                <div className="header-main">
                    <div className="logo">
                        <Link to='/'>
                             <img src={logo} />
                        </Link>
                    </div>
                    <div className="search">
                        <div className="search-form">
                            <input type='text' placeholder='ssd 240gb' className="search-input" onChange={e => setSearch(e.target.value)}/>
                            <select id="cars">
                                <option>All Categories</option>
                                {categories.data.map(category => (
                                    <option>{category.name}</option>
                                ))}
                            </select>
                            <Link to={search.length == 1 ? '#' : `/search_${search}` } className="search-button">
                                <FontAwesomeIcon icon={faSearch} color="#fff" size="1x"/>
                            </Link>
                        </div>
                        <div class="hot-words">
                            <a href="//www.aliexpress.com/w/wholesale-winter-set.html?spm=a2g0o.best.100000001.1.3db02c25wJKeWB">winter set</a>
                            <a href="//www.aliexpress.com/w/wholesale-warsaw.html">warsaw</a>
                            <a href="//www.aliexpress.com/w/wholesale-y2k-clothes.html" >y2k clothes</a>
                            <a href="//www.aliexpress.com/w/wholesale-sexy-underwear.html" >men underwear</a>
                            <a href="//www.aliexpress.com/w/wholesale-xiaomi-redmi-note-9.html" >xiaomi redmi note 9</a>
                            <a href="//www.aliexpress.com/w/wholesale-snapdragon-845.html">snapdragon 845</a>
                        </div>
                    </div>
                    <Link to="/cart" className="header-items">
                        <span class="right-cart-number" id="right-cart-num">{props.state.shop.items.length}</span>
                    </Link>
                </div>
            </div>
        </div>
    )
}

const mapStateToProps = state => ({
    state
});

  export default connect(mapStateToProps)(Header);
